/* Rubik font */
@import url('https://fonts.googleapis.com/css2?family=Rubik:ital,wght@0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');

:root {
	--sfss-red: #ae2024;
	--sfss-red-light: #ee6064;
	--sfss-red-lighter: #ffa0a4;
	--sfss-blue: #005e8a;
}

html {
	scroll-padding-top: 72px;
}

a.title {
	display: block;
	font-family: 'Rubik', sans-serif;
	font-weight: 400;
	font-size: 16pt;
	white-space: nowrap;
	color: white;
	text-decoration: none;
}

@media (max-width: 768px) {
	a.title {
		font-size: 14pt;
	}
}

a.title b {
	font-weight: 700;
}

.mdb-button.primary .mdb-icon {
	--mdb-icon-stroke-width: 3px;
}

.submit-email h3 {
	margin: 8px 0;
	font-size: 16pt;
}

.submit-email p {
	margin: 8px 0;
	text-indent: 32px;
}

.submit-email .mdb-icon {
	width: 20px;
	height: 20px;
	--mdb-icon-stroke-width: 4px;
}

.submit-email .mdb-icon.rotate {
	transform: rotate(-90deg);
}

input {
	height: 24px;
	line-height: 24px;
	font-size: 14pt;
	background-color: #f8f8f8;
	padding: 4px 8px;
	border: solid 1px #a0a0a0;
	border-radius: 4px;
}

input::placeholder {
	font-style: italic;
}

.leading {
	position: relative;
	width: 100vw;
	height: 70vh;
}

.leading .mdb-gallery-slideshow {
	z-index: 1;
	width: 100%;
	height: 100%;
}

.leading h1 {
	margin: 0;
	font-size: 32pt;
	font-weight: 500;
	color: white;
}

.leading h1 b {
	font-weight: 900;
}

.leading h2 {
	margin: 0;
	font-size: 20pt;
	font-weight: 300;
	color: white;
}

.leading h3, a {
	margin: 8px 0;
	font-size: 12pt;
	font-weight: 400;
	color: white;
	text-decoration: none;
}

.leading h3 .mdb-icon {
	--mdb-icon-color: white;
}

.page-container {
	width: min(100vw - 32px, var(--mdb-page-width));
	margin-left: auto;
	margin-right: auto;
}

.page-container hr {
	margin: 24px 0;
	border: none;
	width: 100%;
	border-bottom: 2px solid #e0e0e0;
}

#main h1 {
	margin: 32px 0;
	font-size: 24pt;
	font-weight: 600;
	color: var(--sfss-blue);
}

#main h2 {
	margin: 24px 0;
	font-size: 16pt;
	font-weight: 600;
	color: var(--sfss-blue);
}

#main p:not(.prompt) {
	margin: 16px 0;
	font-size: 14pt;
	font-weight: 400;
	text-indent: 48px;
}

#main p.prompt {
	margin: 0;
	margin-top: 24px;
	text-align: center;
	font-size: 12pt;
	font-weight: 400;
	font-style: italic;
}

.footer {
	width: calc(100vw - 64px);
	margin-top: 32px;
	padding: 32px;
	background-color: black;
	color: white;
}

.footer p {
	margin: 4px 0;
}

.mdb-button.navbar {
	background-color: white;
	color: var(--sfss-red);
	font-weight: 800;
}

.mdb-button.navbar:hover {
	background-color: var(--sfss-red-lighter);
}

.mdb-button.navbar:hover:active {
	box-shadow: 0 0 0 1px white;
	background-color: var(--sfss-red-lighter);
}

.mdb-button.navbar .mdb-icon {
	--mdb-icon-color: var(--sfss-red);
	--mdb-icon-stroke-width: 3px;
}

.mdb-button.navbar-transparent {
	box-shadow: 0 0 0 1px var(--sfss-red-light);
	background-color: var(--sfss-red);
	color: white;
}

.mdb-button.navbar-transparent:hover,
.mdb-button.navbar-transparent.selected {
	box-shadow: 0 0 0 1px var(--sfss-red-lighter);
	background-color: var(--sfss-red-lighter);
}

.mdb-button.navbar-transparent:hover:active {
	box-shadow: 0 0 0 1px white;
	background-color: var(--sfss-red-light);
}

.mdb-button.navbar-transparent .mdb-icon {
	--mdb-icon-color: white;
	--mdb-icon-stroke-width: 2px;
}
